const initialState = {
    items: [
        { id: 2, price: 40, name: "Milk" },
        { id: 3, price: 10000, name: "kindle" },
        { id: 4, price: 2500, name: "Sunglasses" }
    ]
}

export const rootReducer = (state = initialState, action) => {
    // console.log(action.id)
    if (action.type === "ADD_ITEM") {
        console.log(action.id, action.name, action.price)
        let addedItems = [
            ...state.items,
            { id: action.id, name: action.name, price: action.price }
        ]
        return {
            ...state,
            items: addedItems
        }
    }
    if (action.type === "DELETE_ITEM") {

        let updatedItems = state.items.filter(item => action.id !== item.id)
        return {
            ...state,
            items: updatedItems
        }
    }
    return state;
}



