import React from 'react';
import './App.css';
import { connect } from 'react-redux'
import { deleteItem } from './actions/itemActions'
import { addItem } from './actions/addItem'
import Button from 'react-bootstrap/Button'
import InputGroup from 'react-bootstrap/InputGroup'
import FormControl from 'react-bootstrap/FormControl'
import Alert from 'react-bootstrap/Alert'

class App extends React.Component {
  constructor() {
    super();
    this.state = {
      name: '',
      price: '',
      GST: 18,
      GSTPrice: '',
      total: '',
      grandTotal: '',
      generateBill: false,
      error: false
    }

  }
  handleChange = (event) => {
    this.setState({
      [event.target.name]: event.target.value
    })
  }
  iDeletePosts = (id) => {
    this.props.deleteItem(id)
    if (this.state.generateBill) {
      this.setState({ generateBill: false })
    }
  }
  addItem = () => {
    if (this.state.name !== '' && this.state.price !== '') {
      this.setState({ error: false })
      if (this.state.generateBill) {
        this.setState({ generateBill: false })
      }
      this.props.addItem(Date().valueOf(), this.state.price, this.state.name)
    } else {
      this.setState({ error: true })
    }
  }
  generateBill = () => {
    const { items } = this.props
    if (items.length) {
      let total = (items.map((item) => parseInt(item.price))
        .reduce((a, b) => a + b))
      let GSTPrice = parseInt(((total / 100) * this.state.GST).toFixed(3))
      let grandTotal = parseInt(total) + parseInt(GSTPrice)
      this.setState({
        generateBill: true,
        total,
        GSTPrice,
        grandTotal
      })
    } else {
      this.setState({
        total: '',
        GSTPrice: '',
        grandTotal: '',
        generateBill: false,
        error: true
      })
    }
  }
  render() {
    const { items } = this.props
    const itemList = items.length ? (
      items.map(item => {
        return (<div key={item.id}>
          <InputGroup key={item.id} >
            <FormControl
              onChange={this.handleChange}
              name='name'
              placeholder={item.name}
              aria-label="Item"
              aria-describedby="basic-addon2"
            />
            <FormControl

              onChange={this.handleChange}
              name='price'
              placeholder={item.price}
              aria-label="Price"
              aria-describedby="basic-addon2"
            />
            <InputGroup.Append>
              <Button onClick={() => { this.iDeletePosts(item.id) }} variant="outline-secondary">Delete</Button>
            </InputGroup.Append>
          </InputGroup>
        </div>
        )
      })
    ) : (
        <div className="center">No  items to show</div>
      );
    return (
      <div >
        <br />
        <h1 className="center">Bill Generation App</h1>

        <div className='bill-container'>
          {/* LEFT CONTAINER */}
          <div className='bill-left-container'>
            <h3>Add New Item</h3>
            <InputGroup>
              <FormControl
                onChange={this.handleChange}
                name='name'
                placeholder="Item"
                aria-label="Item"
                aria-describedby="basic-addon2"
              />
              <FormControl
                type="number"
                onChange={this.handleChange}
                name='price'
                placeholder="Price"
                aria-label="Price"
                aria-describedby="basic-addon2"
              />
              <InputGroup.Append>
                <Button onClick={this.addItem} variant="outline-secondary">ADD NEW</Button>
              </InputGroup.Append>
            </InputGroup>
            {this.state.error ?
              <Alert variant="danger" onClose={() => this.setState({ error: false })} dismissible>
                <Alert.Heading>Oh snap! You got an error!</Alert.Heading>
                <p>
                  Change this and that and try again. Duis mollis, est non commodo
                  luctus, nisi erat porttitor ligula, eget lacinia odio sem nec elit.
                  Cras mattis consectetur purus sit amet fermentum.
                   </p>
              </Alert> : null
            }
            <hr />
            {itemList} <hr />
            <InputGroup className="mb-3">
              <InputGroup.Prepend>
                <InputGroup.Text>GST</InputGroup.Text>
                <FormControl
                  onChange={this.handleChange}
                  name='GST'
                  value={this.state.GST}
                  placeholder="GST"
                  aria-label="GST"
                  aria-describedby="basic-addon2" />
                <InputGroup.Text>%</InputGroup.Text>
              </InputGroup.Prepend>

            </InputGroup>
            <InputGroup>

              <InputGroup.Append>
                <Button onClick={this.generateBill} variant="outline-secondary">Generate Bill</Button>
              </InputGroup.Append>
            </InputGroup>
          </div>

          {/* RIGHT CONTAINER */}
          <div className='bill-right-container'>
            {this.state.generateBill ?
              <div>
                <h3>Final Bill</h3>
                <InputGroup>
                  <FormControl
                    name='name'
                    id='input'
                    placeholder="Item"
                    aria-label="Item"
                    aria-describedby="basic-addon2"
                  />
                  <FormControl
                    type="number"
                    id='input'
                    name='price'
                    placeholder="Price"
                    aria-label="Price"
                    aria-describedby="basic-addon2"
                  />
                  <InputGroup.Append>
                    <Button variant="outline-secondary">Delete</Button>
                  </InputGroup.Append>
                </InputGroup><hr />
                {itemList}
                <br />
                <div>
                  <div className="space-between">
                    <h6>Number of Items: </h6>
                    <h6>₹{this.props.items.length}</h6>
                  </div>
                  <div className="space-between">
                    <h6>Total: </h6>
                    <h6>₹{this.state.total}</h6>
                  </div>
                  <div className="space-between">
                    <h6>GST {this.state.GST}%:</h6>
                    <h6>₹{this.state.GSTPrice}</h6>
                  </div>
                  <div className="space-between">
                    <h5>Grand Total:</h5>
                    <h5>₹{this.state.grandTotal}</h5>
                  </div>
                </div>

                <hr />
              </div> : null
            }
          </div>
        </div>
      </div>
    )

  }
}

const mapStatetoProps = (state) => {
  return {
    items: state.items
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    deleteItem: (id) => { dispatch(deleteItem(id)) },
    addItem: (id, price, name) => { dispatch(addItem(id, price, name)) }
  }
}


export default connect(mapStatetoProps, mapDispatchToProps)(App);
